import React from 'react';
import { AppContext } from '../../../AppContext';

export const Projects: React.FC = () => {
  return (
    <AppContext.Consumer>
      {({ personalData }) => (
        <div>
          <h3>{personalData.labels.sectionLabels.projects}</h3>
          <div>
            {personalData.projects.map((item) => (
              <div key={item.name}>{item.name}</div>
            ))}
          </div>
        </div>
      )}
    </AppContext.Consumer>
  );
};
